<template>
    <div class="incidents">
      <AppTable isSticky :gridColumns="gridColumns" :data="jsonData" />
    </div>
  </template>
  
  <script>
  import AppTable from '../../Organisms/Table/AppTable.vue';
  import GridText from '../../Atoms/Text/GridText.vue'
  import axios from 'axios'; // Import Axios
  import IncidentRule from '@/components/Molecules/IncidentRule/IncidentRule.vue';
  import AppTag from '@/components/Atoms/AppTag/AppTag';
  import UserProfile from '@/components/Molecules/UserProfile/UserProfile'
  import IncidentCategory from '@/components/Molecules/IncidentCategory/IncidentCategory';

  const GRID_COLUMNS = [
        {
            id: 'id',
            key: 'id',
          label: 'Id',
            component: GridText,
            componentProps: {
        },
        visible: true
          },
          {
              id: 'title',
              key: 'title',

        label: 'Event Title',
              component: GridText,
              componentProps: {
                isBold: true,
              },
        visible: true
          },
          {
              id: 'comment_threads',
              key: 'comment_threads',
               label: 'Comments',
              headerTitle: 'Comments',
              componentProps: {
                  gridColumns: [{ key: 'user', component: UserProfile}, 'message']
        },
        component:  AppTable,
        visible: true
          },
          {
              id: 'rule',
              key: 'rule',
        label: 'Rules',
              headerTitle: 'Rules',
              componentProps: {
                isBold: true,
              },
        component:  IncidentRule,
        visible: true
          },
          {
              id: 'violations_stakeholders',
              key: 'violations_stakeholders',
              label: 'Stakeholders',
              headerTitle: 'Stakeholders',
              component:  AppTable,
              visible: true
          },
          {
              id: 'violations_tags',
              key: 'violations_tags',
              label: 'Tags',
              headerTitle: 'Tags',
              component:  AppTag,
              visible: true
          },
          {
              id: 'violations_categories',
              key: 'violations_categories',
              label: 'Categories',
              headerTitle: 'Categories',
             
              component: IncidentCategory ,
              visible: true
            }


            
        ]
  export default {
    components: {
      AppTable,
    },
    data() {
      return {
        gridColumns: [],
        jsonData: {
          data: {
            violations_list: [],
          },
        },
      };
    },
    created() {
      // Fetch the JSON data from the file
      this.gridColumns = GRID_COLUMNS;
      axios.get('./eventData.json')
        .then(response => {
          this.jsonData = response.data.data.violations_list.map(data => {
            data.violations_stakeholders = data.violations_stakeholders.map(d => d.user)
            data.violations_tags = data.violations_tags.map(d => d.tag)
            data.violations_categories = data.violations_categories.map(d => d.category)
            data.rule.rules_cameras = data.rule.rules_cameras.map(d => d.camera)
            return data
          });
          console.log( this.jsonData)
        })
        .catch(error => {
          console.error('Error fetching JSON data:', error);
        });
    },
    mounted() {
      console.log(this.jsonData)
    }
  };
  </script>
  <style>
  .incidents .b-table-sticky-header{
    max-height: 900px !important;
  }
</style>